/* Main styles */
/* Header */
.menu {
  background: #333;
  overflow: hidden;
}

.menu__item {
  float: left;
}

.menu__item--right {
  float: right;
}

@media only screen and (max-width: 768px) {
  .menu__item {
    float: none;
  }
}

.menu__link {
  display: block;
  color: #fff;
  padding: 15px 20px;
  text-align: center;
  text-decoration: none;
  text-transform: uppercase;
}

.menu__link:hover:not(.menu__link--active) {
  background-color: #111;
}

.menu__link--active {
  background-color: #4caf50;
}

.testimonials__title {
  text-align: center;
  text-transform: uppercase;
  font-size: 2rem;
}

.testimonials__list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}

.testimonials__item {
  margin: 10px;
  width: calc(20% - 20px);
  flex: auto;
  max-width: 60%;
}

.testimonial {
  min-width: 250px;
  padding: 20px;
  border-radius: 4px;
  background-color: #ff9800;
  text-align: center;
  box-shadow: 1px 1px 2px rgba(0, 0, 0, 0.4);
}

.testimonial__img {
  width: 80px;
  height: 80px;
  margin: 0 auto;
}

.testimonial__img img {
  width: 80px;
  height: 80px;
  border: 2px solid #fff;
  border-radius: 50%;
}

.testimonial__name {
  font-size: 2rem;
  font-weight: bold;
}

.testimonial__job {
  color: gray;
}

.testimonial__descr p {
  margin-top: 10px;
}

.raiting__star {
  display: inline-block;
  margin: 5px 2px;
  width: 18px;
  height: 18px;
}

.raiting__star:hover {
  cursor: pointer;
  transform: scale(1.2);
  opacity: 0.5;
}

.symbol-star {
  transition: all 0.3s ease-in-out;
  cursor: pointer;
  margin: 5px 2px;
}

.symbol-star:hover {
  fill: #fff;
  transform: scale(1.2);
}

.challenge {
  width: 800px;
  min-height: 1200px;
}

.challenge1 {
  border: 30px solid #fff;
  background-image: repeating-linear-gradient(45deg, #fff 0, #fff 50px, #fff000 50px, #fff000 100px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.challenge1:hover {
  animation: bgMove 0.4s ease infinite;
}

.challenge1__text {
  color: #ff00ea;
  font-size: 148px;
  font-weight: bold;
  text-transform: uppercase;
  transform: rotate(-90deg);
  position: relative;
}

.challenge1__text:after {
  content: 'ua web challenge';
  display: block;
  color: rgba(0, 180, 255, 0.6);
  mix-blend-mode: multiply;
  position: absolute;
  top: 0;
  left: 10px;
}

@keyframes bgMove {
  0% {
    background-image: repeating-linear-gradient(45deg, #fff 0, #fff 50px, #fff000 50px, #fff000 100px);
  }
  25% {
    background-image: repeating-linear-gradient(45deg, #fff 25px, #fff 75px, #fff000 75px, #fff000 125px);
  }
  50% {
    background-image: repeating-linear-gradient(45deg, #fff 50px, #fff 100px, #fff000 100px, #fff000 150px);
  }
  75% {
    background-image: repeating-linear-gradient(45deg, #fff 75px, #fff 125px, #fff000 125px, #fff000 175px);
  }
  100% {
    background-image: repeating-linear-gradient(45deg, #fff 100px, #fff 150px, #fff000 150px, #fff000 200px);
  }
}

.challenge2 {
  background: #000 url("../img/temp/dog-red.svg") 50% 50% no-repeat;
}

.challenge2__text {
  padding: 50px 60px 170px 60px;
  column-count: 2;
  column-gap: 60px;
  font: 13px/16.5px Georgia, Times;
  background-color: white;
  mix-blend-mode: screen;
}

.challenge2__text p {
  margin-bottom: 20px;
  text-indent: -25px;
}

.challenge2__text p:first-child:first-letter {
  font-size: 83px;
  display: inline-block;
  vertical-align: top;
}

.challenge3 {
  background: url("../img/temp/skull.svg") 50% 50% no-repeat;
  background-position: 176% 2%;
}

.text--float {
  float: right;
  shape-outside: url("data:image/svg+xml;base64,");
  width: 200px;
  height: 900px;
}

.arrow1 {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 25px 50px 25px 0;
  border-color: transparent blue transparent transparent;
}

.arrow2 {
  width: 0;
  height: 0;
  border-style: solid;
  border-width: 40px 10px 0 10px;
  border-color: green transparent transparent transparent;
}

